import { useState } from "react";

function Navbar({ menuItems, onSelect }) {
  const [open, setOpen] = useState(false);

  return (
    <nav style={styles.navbar}>
      <div style={styles.logo}>Data i</div>

      {/* Hamburger button for mobile */}
      <button style={styles.hamburger} onClick={() => setOpen(!open)}>
        &#9776;
      </button>

      {/* Menu items */}
      <ul style={{ ...styles.menu, ...(open ? styles.menuOpen : {}) }}>
        {menuItems.map((item) => (
          <li
            key={item.name}
            style={styles.menuItem}
            onClick={() => {
              onSelect(item.image);
              setOpen(false); // close menu on mobile
            }}
            onMouseOver={(e) => (e.target.style.color = "#007bff")}
            onMouseOut={(e) => (e.target.style.color = "#000")}
          >
            {item.name}
          </li>
        ))}
      </ul>
    </nav>
  );
}

const styles = {
  navbar: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    padding: "1rem 2rem",
    position: "sticky",
    top: 0,
    backgroundColor: "#fff",
    zIndex: 1000,
    flexWrap: "wrap",
  },
  logo: {
    fontSize: "1.5rem",
    fontWeight: "bold",
  },
  hamburger: {
    display: "none",
    fontSize: "28px",
    background: "none",
    border: "none",
    cursor: "pointer",
  },
  menu: {
    display: "flex",
    gap: "1.5rem",
    listStyle: "none",
    margin: 0,
    padding: 0,
  },
  menuOpen: {
    display: "flex",
    flexDirection: "column",
    width: "100%",
    marginTop: "10px",
  },
  menuItem: {
    cursor: "pointer",
    fontWeight: 500,
    color: "#000",
    transition: "color 0.2s",
  },
};

// Media query for mobile
const mediaQuery = window.matchMedia("(max-width: 768px)");
if (mediaQuery.matches) {
  styles.hamburger.display = "block";
  styles.menu.flexDirection = "column";
}

export default Navbar;
